@import "../variables";

.fixed-social-share {
  z-index: 999999;
  transform: translateX(-10rem);
  display: flex;
  align-items: center;
  background-color: var(--box-background-color);
  box-shadow: 0 0.5rem 1rem hsla(0, 0%, 0%, 0.15) !important;
  position: fixed;
  left: 0;
  top: 40%;
  padding-top: calc(1px + 0.3vw);
  padding-bottom: calc(1px + 0.3vw);
  padding-left: 0.5rem;
  border-top-right-radius: 0.5rem;
  border-bottom-right-radius: 0.5rem;
  transition: 0.5s ease-in-out;

  .toggle-icon {
    background-color: $primary;
    border-radius: 0.5rem;
    padding: calc(0px + 0.3vw) calc(0px + 0.3vw);
    font-size: 20px;
    color: white;
    right: 0;
    margin: 0rem calc(5px + 0.3vw);
    cursor: pointer;
  }

  .rotate-left {
    transition: 0.5s ease-in-out;
    transform: rotate(180deg);
  }
  .rotate-right {
    transition: 0.5s ease-in-out;
    transform: rotate(0deg);
  }

  .fa,
  .fab,
  .fas {
    font-size: calc(0.8rem + 0.6vw);
    color: var(--color);
    right: 0;
    padding: 0.5rem;
  }

  .icon {
    display: flex;
  }
}

@media screen and (min-width: $breakdown-md) {
  .fixed-social-share {
    transform: translateX(-12rem);
    left: 0;
    top: 40%;
  }
}

.sticky-social-share {
  display: none;
}

@media screen and (min-width: $breakdown-md) {
  .sticky-social-share {
    display: block;
    position: fixed;
    left: 0;
    top: 50%;
    transform: translate(0, -50%);
    background-color: var(--box-background-color);
    padding: 1rem 0.5rem;
    border-radius: 0 0.5rem 0.5rem 0;
    box-shadow: 0px 0 10px 2px #00000010;
    z-index: 1;
    font-size: calc(1.2rem + 0.3vw);
    display: flex;
    flex-direction: column;
    gap: 1rem;

    a,
    i {
      color: var(--pcolor);
    }
  }
}
